@import "../../common/color.scss";

.movie-section {
  display: flex;
  justify-content: space-evenly;
  padding: 2.5rem 0rem;
  color: $font-primary;
  font-weight: 400;
}

.movie-rating {
  padding-left: 0.25rem;
  margin-top: 1.2rem;
  color: $font-secondry;
  display: flex;
  span {
    margin-right: 1.2rem;
  }
}

.movie-title {
  font-size: 2.5rem;
  color: $font-primary;
}

.movie-plot {
 margin-top: 1.2rem;
  line-height: 1.8rem;
}

.movie-info > div span:first-child{
    padding: .6rem 0rem;
    color: $font-primary;
    font-weight: 600;
    width: 8rem;
    display: inline-block;
}

.movie-info > div span {
    color: $font-secondry;
}

.fa-star{
    color: #ff9a00;
}

.fa-thumbs-up{
    color: #fafafa;
}

.fa-star{
    color: rgb(191, 213, 214);
}

.fa-star{
    color: peachpuff;
}

.section-right{
    margin-left: 1.8rem;
}