@import "../../common/color.scss";

.header {
  background-color: $secondry-color;
  height: 6rem;
  padding: 0 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  color: $font-primary;
  font-size: 1.5rem;
  font-weight: 600;
}

.linko {
  text-decoration: none !important;
}

.user-image,
.user-image img {
  width: 4rem;
  height: 4rem;
}

.search-bar {
  width: 50%;
  display: flex;
  justify-content: center;

  form {
    display: flex;
    justify-content: center;
    width: 70%;
    button {
      padding: 0rem .5rem;
      font-size: 1.2rem;
      cursor: pointer;
      height: 2.5rem;
    }
  }

  input{
      font-size: 1.2rem;
      width: 100%;
      padding: .25rem .25rem .25rem .5rem;
      height: 2.5rem;
      outline: none;
  }
}
