@import "../../common/color.scss";

.card-item {
  background: $secondry-color;
  cursor: pointer;
  transition: all 0.3s;
  min-height: 30rem;
  height: 100%;
  margin:.6rem;

  &:hover {
    transform: scale(1.05);
    transition: all 0.3s;
  }
}

.card-top {
  height: 300px;

  img {
    width: 100%;
    height: 100%;
  }
}

.card-bottom {
  padding: 1rem;
  text-align: center;
}

.card-info {
  color: $font-primary;
  h4 {
    font-size: 1rem;
    font-weight: 400;
    margin-bottom: 1rem;
  }
  p {
    text-align: center;
  }
}
